.section {
  background-color: $header-text-color;
  min-height: 900px;
  padding-top: 20px;
  padding-bottom: 20px;

  // @media screen and (min-width: 768px) {
  //     padding-top: 60px;
  //     padding-bottom: 60px;
  // }
}

.container {
  padding: 0 20px;
  margin: 0 auto;

  @media screen and (min-width: 320px) {
    width: 320px;
    margin: 0 auto;
  }

  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    width: 768px;
    padding: 0 32px;
  }

  @media screen and (min-width: 1024px) {
    width: 1024px;
  }
}

.main__button-list {
  display: flex;
  margin: 0 auto 20px;

  @media screen and (min-width: 768px) {
    margin: 0 auto 32px;
  }
}

.main__button-item:first-child {
  margin-right: 20px;

  @media screen and (min-width: 768px) {
    margin-right: 32px;
  }

  @media screen and (min-width: 1024px) {
    margin-right: 16px;
  }
}

.main__button {
  width: 130px;
  height: 44px;
  border-radius: 5px;
  padding: 15px;

  text-transform: uppercase;
  font-size: 12px;
  line-height: 1.33;
  font-weight: 500;

  border: 1px solid $main-text-color;
  background-color: $header-text-color;

  @include respond-to(tablet) {
    width: 152px;
  }

  @include respond-to(desktop) {
    width: 147px;
    height: 42px;
  }

  @include transit(background-color, color, border-color);

  &:hover,
  &:focus {
    background-color: $accent-color;
    color: $header-text-color;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
  }
}

