.main__movie-card-list {
  width: 100%;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    margin-right: calc(-1 * 32px);
  }

  @media screen and (min-width: 1024px) {
    margin-right: calc(-1 * 16px);
  }
}

.main__movie-card-item {
  width: 100%;

  @media screen and (min-width: 768px) {
    width: calc((100% - 32px * 2) / 2);
    margin-right: 16px;
    margin-left: 16px;
  }

  @media screen and (min-width: 1024px) {
    width: calc((100% - 16px * 3) / 3);
    margin-right: 8px;
    margin-left: 8px;
  }

  &:not(:last-child) {
    margin-bottom: 20px;

    @media screen and (min-width: 768px) {
      margin-bottom: 0;
    }
  }

  &:not(:nth-last-child(-n + 2)) {
    @media screen and (min-width: 768px) {
      margin-bottom: 32px;
    }

    @media screen and (min-width: 1024px) {
      margin-bottom: 0;
    }
  }

  &:not(:nth-last-child(-n + 3)) {
    @media screen and (min-width: 1024px) {
      margin-bottom: 32px;
    }
  }
}

.main__movie-img {
  margin-bottom: 10px;
  width: 100%;
  height: 450px;
  border-radius: 5px;
  cursor: pointer;

  @media screen and (min-width: 1024px) {
    margin-bottom: 8px;
  }
}

.main__movie-title {
  text-transform: uppercase;
}

.main__movie-title,
.main__movie-genre {
  // color: $main-text-color;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
}

.main__movie-genre {
  display: inline;
  color: $accent-color;
}

.main__movie-year::before {
  content: '';
  width: 1px;
  height: 10px;
  margin: 0 3px;
  display: inline-block;
  background-color: $accent-color;
}

.main__movie-raiting {
  // Для вкладки MyLibrary:
  // display: inline;
  display: none;
  margin-left: 8px;
  padding-right: 9px;
  padding-left: 9px;
  color: $header-text-color;
  background-color: $accent-color;
  border-radius: 5px;
  font-size: 12px;
  line-height: 1.16;
  font-weight: 500;
}

.main__search-error {
  font-weight: 700;
  font-size: 24px;
  line-height: 1.14;
  margin: 20px auto 0;
  text-align: center;
  color: #ff001b;
}
